<template>
  <div class="checkout">
    <div class="container-fluid">
      <div class="row">
        <div class="col-8"></div>
        <div class="col-4">
          <div style="background-color: #f5f5f5; padding: 20px 40px">
            <div
              class="product-details"
              style="
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-bottom: 5px;
                padding: 10px;
              "
              v-for="item in cartItems"
              :key="item.id"
            >
              <div
                style="display: flex; align-items: center; position: relative"
              >
                <img
                  :src="item.thumbnail"
                  alt=""
                  width="60px"
                  style="
                    background-color: white;
                    border-radius: 10px;
                    border: 1px solid #ccc;
                    margin-right: 10px;
                  "
                />
                <span
                  data-v-5b6fbb5e=""
                  class="position-absolute top-0 start-0 translate-middle bg-secondary rounded-circle text-center"
                  style="
                    width: 20px;
                    height: 20px;
                    color: white;
                    line-height: 20px;
                    font-size: 12px;
                  "
                  >{{ item.quantity }}</span
                >
                <div class="title-parent" style="font-size: 16px">
                  <p class="m-0">{{ item.title }}</p>
                  <p class="m-0">{{ item.category }}</p>
                </div>
              </div>

              <span style="display: inline-block"
                >${{
                  Math.ceil(
                    item.price - item.price * (item.discountPercentage / 100)
                  )
                }}</span
              >
            </div>
            <div
              class="discount-code"
              style="
                padding: 10px;
                display: flex;
                justify-content: space-between;
                align-items: center;
              "
            >
              <input
                type="text"
                placeholder="Discount code"
                style="
                  padding: 8px;
                  border: 1px solid #ccc;
                  outline: none;
                  width: 80%;
                "
              />
              <button
                style="background-color: #eee; padding: 8px 12px; border: none"
              >
                Apply
              </button>
            </div>
            <div class="total" style="padding: 10px">
              <div
                style="
                  display: flex;
                  align-items: center;
                  justify-content: space-between;
                "
              >
                <span style="font-weight: lighter; font-size: 14px"
                  >Subtotal</span
                ><span>{{ totalItems }}$</span>
              </div>
              <div
                style="
                  display: flex;
                  align-items: center;
                  justify-content: space-between;
                "
              >
                <span style="font-weight: lighter; font-size: 14px"
                  >Shipping</span
                ><span>20$</span>
              </div>
              <div
                style="
                  display: flex;
                  align-items: center;
                  justify-content: space-between;
                  font-weight: bold;
                "
              >
                <span>Total</span><span>{{ totalItems + 20 }}$</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { cartStore } from "@/stores/cart";
import { mapState } from "pinia";
export default {
  name: "checkout_page",
  computed: {
    ...mapState(cartStore, ["cartItems"]),
    totalItems() {
      let total = 0;
      this.cartItems.forEach((item) => {
        total +=
          Math.ceil(item.price - item.price * (item.discountPercentage / 100)) *
          item.quantity;
      });
      return total;
    },
  },
};
</script>
